
.container.title-section {
  background-color: rgba(3, 44, 60, 0.8);
  color: white;
  padding-top: 15px;
  padding-bottom: 15px;
}

section h1,h2,h3,h4 {
  color: rgba(0, 0, 0, 9);
}

section.title-section {
  h1,h2,h3,h4 {
    color: white;
  }
}

.ant-tabs .ant-tabs-tab {
  padding: 5px 0;
}

.ant-tabs > div {
  > .ant-tabs-nav  {
    margin: 0;
    margin-bottom: 4px;
    
    &::before {
      color: red;
      border: none;
    }
  }
}

.anchors-container {
  z-index: 100;
  position: relative;
}

.main-writers {
  display: flex;
}

.writers {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px 10px;
  flex-wrap: wrap;
}

.writers_notes {
  margin: 15px 0;
}

.writer-item { 
  display: flex;
  flex-direction: column;
  margin: 5px 10px;
  align-items: center;
  justify-content: flex-start;
  max-width: 200px
}

.writer-name {
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 500;
}

.writers_notes { 
  margin-top: 15px;
}

.anchors {
  display: flex;
  justify-content: center;
  width: 100%;
  background: white;

  .ant-anchor {
    background-color: white;
    padding-top: 5px;
    padding-bottom: 5px;
  } 
}

.links { 
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0;
}

.ant-tag {
  padding: 5px;
  font-size: 15px;
}

.reference-icon {
  font-size: 22px;
}

.link-item {
  margin: 0 10px;
  color: white;
}

.title { 
  margin: 15px 0;
}

.link-icon {
  height: 75px;
  width: 75px;
  margin-bottom: 5px;
}

.qoute_code {
  height: 300px;
  display: block;
  overflow: auto;
}

.abstract {
  line-height: 130%;
  // margin: 0 7%;
}

section + section {
  border-top: 1px solid rgba(black, 0.01)
}

.examples_group + .examples_group{
  border-top: 1px solid rgba(black, 0.05);
  margin-top: 10px;
  padding-top: 15px;
}

section.examples {
  background: rgba(black, 0.02);

  .slick-dots{
    display: None;
  }

  .slick-dots li button {
    background-color: rgba(white, 1);
  }

  .slick-dots li.slick-active button {
    background-color: white;
  }

  .examples_description {
    margin: 10px 0 20px 0;
  }

  .example_items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

  }

  .example_item {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    margin-bottom: 40px;
    transform: translate(500%);

    @media (max-width: 464px) {
      transform: translate(450%);
    }

    .example_images { 
      display: flex;
      margin-bottom: 10px;

      .example_image {
        display: flex;
        flex-direction: column;
        justify-items: center;
        align-items: center;
        max-width: 50%;
      }
      
      > * {
        margin: 0 12px;
        text-align: center;
        font-weight: 600;
      }

      img {
        border: 1px solid rgba(white, 0.8);
        display: block;
        max-width: 100%;
        
      }
    }
  }
}